.page-header{
    z-index:1000;
    position:sticky;
    height:0px;
    top :0;
    left:0;
    right:0;
    .navigation-bar{
        padding: 32px 0px;
        .logo{
            margin: 0px 30px 0px 0px;
            padding: 0px 15px;
            float:left;
        }
        .navigation-bar-item{
            ul {
                list-style-type: none;
                margin: 0;
                padding: 0;
                overflow: hidden;
                padding : 0px 15px;
                li {
                    float: left;
                    a {
                        display: flex;
                        color:map-get($color,text-black);
                        text-align: center;
                        padding: 14px 16px;
                        text-decoration: none;
                        &:hover:not(.active) {
                            background-color:map-get($color,home-hover);
                        }
                    }
                }
            }
        }
    }
}
